body{
  background-color: rgb(241, 235, 229);
}
.main_form {
  margin-top: 25px;
  gap: 30px;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  gap: 20px;
  box-sizing: border-box;
  line-height: 1.1;
 
}
.form__label {
  text-align: center;
  margin-left: 10px;
  margin-right: 15px;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  @media (min-width: 768px) {
    flex-direction: row;
  }
}

.title {
  text-transform: capitalize;
  color: $greenDark;
  margin-top: 20px;
  margin-bottom: 20px;
}
.input {
  width: 200px;
  border: solid 1px $greenDark;
  border-radius: 25px;
}
.button {
  cursor: pointer;
  border-radius: 50px;
  width: 60px;
  background-color: $greenDark;
  color: $white;
  font-weight: bold;
}

.container {
  display: flex;
  text-align: center;
}
.containerResults {
  text-align: center;

  @media (min-width: 768px) {
    position: relative;
    left: 50px;
    width: 60%;
  }
}
.article {
  box-shadow: 0px 0px 5px 1px grey;
  border: solid 1px $greenDark;
  padding: 10px;
  width: 140px;
  height: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7px;
  border-radius: 15px;
  color: $greenDark;
  cursor: pointer;
}
.article.selected {
  background-color: rgb(113, 175, 113);
  color: $white;
  border: solid 1px $greenDark;
  border-radius: 15px;
}
.article-fav {
  border: solid 1px $greenDark;
  background-color: $greenLight;
  color: $white;
  border-radius: 15px;
  box-shadow: 0px 0px 5px 1px grey;
  padding: 10px;
  width: 140px;
  height: 220;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7px;
}
.favourites,
.result {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 7px;
  @media (min-width: 768px) {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 15px;
    margin-right: 30px;
  }
}
.img,
.img-fav {
  height: 140px;
  width: 120px;
}
.reset {
  position: relative;
  bottom: 10px;
  border-radius: 20px;
  width: 100px;
  height: 25px;
  background-color: $greenLight;
  color: $white;
  font-weight: bold;
  border: solid 1px;
}
